@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(games) {
  @include e(banner) {
    height: 360px;

    :global(.swiper-pagination-bullet) {
      background-color: #fff;
      border: none;
    }

    :global(.swiper-pagination-bullet-active) {
      background-color: #a4201c;
      border: none;
      transition-delay: 0s;
    }
  }

  @include e(prize) {
    height: 120px;
    text-align: center;
    background: repeat-x center;

    @include background-image(url(~@/views/games/img/feature.jpg));
  }

  @include e(prize-item) {
    display: inline-block;
    width: 500px;
    height: 100%;
    font-size: 26px;
    text-shadow: 2px 6px 5px #000;
    vertical-align: top;
    background: no-repeat center;

    @include background-image(url(~@/views/games/img/lights.png));
  }

  @include e(prize-amount) {
    position: relative;
    display: inline-block;
    margin-top: 30px;
    color: #fff;
  }

  @include e(prize-unit) {
    position: absolute;
    left: -28px;
    color: #999;
  }

  @include e(prize-name) {
    margin-top: 10px;
    font-size: 16px;
    color: #fc6;
  }

  @include e(content) {
    width: $--content-width;
    margin: 0 auto;
  }

  @include e(list) {
    position: relative;
    padding-bottom: 40px;
    margin-top: 22px;
  }
}

@include b(tab) {
  height: 44px;
  color: #999;
  border-bottom: 1px solid #323232;

  @include e(inner) {
    display: inline-block;
    vertical-align: top;

    &:hover {
      @include e(item) {
        &:not(:hover) {
          color: inherit;

          &::before {
            width: 0;
          }
        }
      }
    }
  }

  @include e(item) {
    position: relative;
    float: left;
    height: 100%;
    padding: 0 10px;
    margin-right: 46px;
    font-size: 16px;
    line-height: 44px;
    text-align: center;
    transition: color 0.2s ease;

    &::before {
      position: absolute;
      bottom: -1px;
      left: 0;
      width: 0;
      height: 2px;
      content: '\20';
      background-color: #cc2944;
      transition: width 0.2s ease;
    }

    &:hover,
    &.active {
      color: #fff;

      &::before {
        width: 100%;
      }
    }
  }

  @include e(search) {
    position: relative;
    float: right;
    width: 240px;
    height: 34px;
    margin-top: 5px;

    @include background-image(url(~@/views/games/img/search_bg.png));
  }

  @include e(input) {
    width: 100%;
    height: 100%;
    padding: 0 34px 0 16px;
    font-size: 14px;
    color: #999;
  }

  @include e(icon-search) {
    position: absolute;
    top: 0;
    right: 0;
    width: 34px;
    height: 100%;
    background: no-repeat left center;

    @include background-image(url(~@/views/games/img/icon_search.png));
  }
}

@include b(filter) {
  margin-top: 22px;

  @include e(list) {
    height: 28px;
    font-size: 14px;
    line-height: 28px;
    color: #999;

    & + & {
      margin-top: 8px;
    }
  }

  @include e(wrapper) {
    display: inline-block;
    vertical-align: top;

    &:hover {
      @include e(item) {
        &:not(:hover) {
          color: inherit;
          border: 1px solid transparent;
        }
      }
    }
  }

  @include e(item) {
    float: left;
    padding: 0 12px;
    line-height: 28px;
    border: 1px solid transparent;
    border-radius: 3px;
    transition: all 0.2s ease;

    & + & {
      margin-left: 10px;
    }

    &.active,
    &:hover {
      color: $--color-primary;
      border: 1px solid #c1974d;
    }
  }

  @include e(label) {
    float: left;
    width: 94px;
  }
}

@include b(game) {
  position: relative;
  z-index: 1;
  float: left;
  margin-bottom: 8px;
  cursor: pointer;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.4);
  transition: all 0.2s ease;

  @include e(img) {
    width: 100%;
    height: 100%;
    vertical-align: top;
  }

  @include e(flag-img) {
    position: absolute;
    top: 0;
    right: 0;
  }

  @include e(amount) {
    position: absolute;
    top: 10px;
    left: 0;
    width: 158px;
    height: 28px;
    font-size: 16px;
    line-height: 28px;
    color: #fff;
    text-align: center;
    letter-spacing: 1px;
    border-radius: 0 14px 14px 0;

    @include button-background-solid(false);
  }

  @include e(info) {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    line-height: 20px;
    color: #fff;
    background: repeat-x left bottom;

    @include background-image(url(~@/views/games/img/box_show.png));
  }

  @include e(info-inner) {
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 100%;
    padding: 0 15px;
  }

  @include e(name) {
    font-size: 14px;
  }

  @include e(describe) {
    font-size: 12px;
    color: $--color-primary;
  }

  @include e(hot) {
    float: right;
    font-size: 12px;
    color: #fff;
  }

  @include e(icon-hot) {
    display: inline-block;
    width: 16px;
    height: 20px;
    margin-top: -3px;
    margin-left: 8px;
    vertical-align: top;
    background: no-repeat right center;

    @include background-image(url(~@/views/games/img/icon_hot.png));
  }

  @include e(mask) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
    text-align: center;
    cursor: default;
    visibility: hidden;
    background: rgba(10, 10, 10, 0.7);
    opacity: 0;
    transition: all 0.2s ease;
  }

  @include e(mask-name) {
    margin-top: 30px;
    font-size: 16px;
    color: #fff;
  }

  @include e(button) {
    margin-top: 22px;

    @include button(160px, 40px);
    @include button-solid();
  }

  @include e(link) {
    margin-top: 15px;
    font-size: 14px;
    color: $--color-primary;
    text-decoration: underline;
  }

  @include e(disable) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 3;
    font-size: 16px;
    line-height: 18px;
    color: #999;
    text-align: center;
    background-color: rgba(36, 36, 36, 0.8);
  }

  @include e(icon-time) {
    display: inline-block;
    width: 35px;
    height: 40px;
    margin-top: (180px - 68) / 2;
    margin-bottom: 10px;
    vertical-align: top;
    background-position: -116px -247px;

    @include background-image(url(~@/assets/img/common/common_icons.png));
  }

  &.disable {
    cursor: default;
  }

  &:not(.disable):hover {
    transform: translateY(-2px);

    @include e(mask) {
      visibility: visible;
      opacity: 1;
    }
  }

  @include m(max) {
    width: 596px;
    height: 180px;

    & + & {
      margin-left: 8px;
    }
  }

  @include m(small) {
    width: 294px;
    height: 180px;
    margin-left: 8px;

    &:nth-child(4n+3) {
      margin-left: 0;
    }
  }

  @include m(mini) {
    width: 234px;
    height: 131px;
    margin-left: 7px;

    @include e(info-inner) {
      padding: 0 10px;
    }

    @include e(mask-name) {
      margin-top: 20px;
    }

    @include e(button) {
      margin-top: 10px;
    }

    @include e(link) {
      margin-top: 10px;
    }

    @include e(icon-time) {
      margin-top: (130px - 68) / 2;
    }

    &:nth-child(5n+3) {
      margin-left: 0;
    }
  }
}

@include b(no-more-tips) {
  position: absolute;
  bottom: 10px;
  width: 100%;
  line-height: 20px;
  color: #999;
  text-align: center;
}

@include b(loading) {
  position: absolute;
  bottom: 7px;
  left: 50%;
  width: 34px;
  height: 34px;
  margin-left: -17px;
  overflow: hidden;
  background: no-repeat center/14px auto;

  @include background-image(url(~@/views/games/img/logo.png));

  @include e(img) {
    width: 100%;
    height: auto;
    vertical-align: top;
    animation: rotate 2s linear infinite;
  }

  @keyframes rotate {
    0% {
      transform: rotate(0);
    }

    100% {
      transform: rotate(360deg);
    }
  }
}

@include b(win) {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 90px;
  overflow: hidden;
  background: #1b1b1b linear-gradient(0deg, #0a0a0a, #1b1b1b);
  box-shadow: inset 0 1px 20px rgba(0, 0, 0, 0.4);

  @include e(inner) {
    width: $--content-width;
    height: 60px;
    margin: (90px - 60) / 2 auto;
    cursor: default;
  }

  @include e(item) {
    width: $--content-width / 5;
    height: 100%;
  }

  @include e(img-wrapper) {
    position: relative;
    float: left;
    width: 60px;
    height: 100%;
    margin-right: 20px;
    margin-left: 10px;
    overflow: hidden;
    font-size: 0;
    border: 1.5px solid #be964c;
    border-radius: 18px;
  }

  @include e(img) {
    position: relative;
    top: 0;
    left: 50%;
    width: auto;
    height: 100%;
    transform: translateX(-50%);
  }

  @include e(info) {
    float: left;
    width: ($--content-width / 5) - (60 + 20 + 10);
    font-size: 12px;
    line-height: (60px / 3);
    color: #fff;
    white-space: nowrap;

    p {
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  @include e(label) {
    color: #7f7f7f;
  }

  @include e(link) {
    color: $--color-primary;
    text-decoration: underline;
  }
}
